.sans {
  font-family: FranklinBold;
}

.example {
  margin: 1.35em auto;
  text-align: center;
  font-size: 14px;
  line-height: 21px;
}

.example ul {
  display: inline-block;
  list-style-type: none;
  padding: 0 0 0 1.5em;
  margin: 0 auto 0 0;
  text-align: right;
}

#browser,
#browser-assist {
  text-align: justify;
  width: 350px;
  margin: 0 0 0 auto;
  display: inline-block;
  font-family: Imprint MT Pro;
  font-size: 14px;
  line-height: 21px;
}

body {
  font-family: Imprint MT Pro!important;
  height: 100%;
}

.main {
  position: absolute;
  top: 50%;
  left: 50%;
  -webkit-transform: translateX(-50%) translateY(-50%);
  transform: translateX(-50%) translateY(-50%);
  &-landing {
    height: 50rem;
    width: 100rem;
  }
}

.text-center {
  text-align: center;
}

.center {
  max-width: 74rem;
}

a:focus,
a:hover {
  text-decoration: none!important;
}

li {
  list-style: none;
}

.theme-green {
  background: #152d1b;
  color: #eeb7b5;
}

.theme-green .landing-link span.active:before,
.theme-green .landing-link span:after,
.theme-green .landing-link span:before,
.theme-green .landing-link span:hover:before,
.theme-green .nav span.active:before,
.theme-green .nav span:after,
.theme-green .nav span:before,
.theme-green .nav span:hover:before {
  background: #eeb7b5;
}

.theme-green .footer .btn-subscribe {
  color: #eeb7b5;
  border: 1px solid #eeb7b5;
}

.theme-green .footer .btn-subscribe:hover {
  background: #eeb7b5;
  border: 1px solid #152d1b;
  color: #152d1b;
}

.theme-green .footer .fa-facebook,
.theme-green .footer .fa-instagram,
.theme-green .footer .fa-twitter {
  color: #eeb7b5;
}

.theme-green .footer .fa-facebook:hover,
.theme-green .footer .fa-instagram:hover,
.theme-green .footer .fa-twitter:hover {
  color: #ffc8c5;
}

.theme-pink {
  background: #eeb7b5;
  color: #152d1b;
}

.theme-pink .landing-link span.active:before,
.theme-pink .landing-link span:after,
.theme-pink .landing-link span:before,
.theme-pink .landing-link span:hover:before,
.theme-pink .nav span.active:before,
.theme-pink .nav span:after,
.theme-pink .nav span:before,
.theme-pink .nav span:hover:before {
  background: #152d1b;
}

.theme-pink .footer .btn-subscribe {
  color: #152d1b;
  border: 1px solid #18261b;
}

.theme-pink .footer .btn-subscribe:hover {
  background: #152d1b;
  border: 1px solid #18261b;
  color: #eeb7b5;
}

.theme-pink .footer .fa-facebook,
.theme-pink .footer .fa-instagram,
.theme-pink .footer .fa-twitter {
  color: #162d1c;
}

.theme-pink .footer .fa-facebook:hover,
.theme-pink .footer .fa-instagram:hover,
.theme-pink .footer .fa-twitter:hover {
  color: #224a2c;
}

.btn-subscribe {
  cursor: pointer;
  padding: 0.2rem;
  line-height: 28px;
  text-transform: uppercase;
  font-weight: 700;
  transition: all 0.2s ease-in-out;
  font-family: FranklinBold;
  font-size: 1.4rem;
  display: block;
  max-width: 38rem;
  margin: 0 auto;
  text-align: center;
}

.btn-subscribe:hover {
  background: #152d1b;
  border: 1px solid #18261b;
  color: #eeb7b5;
  outline: none;
}

.logo {
  width: 85px;
  position: absolute;
  z-index: 4;
  top: 4rem;
  left: 4rem;
}

.logo img {
  width: 100%;
}

.nav {
  position: absolute;
  top: 4rem;
  left: 50%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  width: 66%;
  text-transform: uppercase;
  font-family: FranklinBold!important;
}

.nav ul {
  list-style: none;
  margin: 0;
  padding: 0;
  text-align: center;
}

.nav ul li {
  display: inline-block;
  width: 19%;
}

.nav ul li a {
  color: inherit;
  text-decoration: none;
  position: relative;
  z-index: 3;
}

.nav span {
  position: relative;
  cursor: pointer;
  width: inherit;
  z-index: 2;
}

.nav span:after,
.nav span:before {
  content: "";
  position: absolute;
  width: 0;
  height: 3px;
  top: 44%;
}

.nav span:before {
  left: -2.5px;
}

.nav span:after {
  right: 2.5px;
  transition: width 0.8s cubic-bezier(.22,.61,.36,1);
}

.nav span:hover:before {
  width: 105%;
  transition: width 0.5s cubic-bezier(.22,.61,.36,1);
}

.nav span:hover:after {
  background: transparent;
  width: 100%;
  transition: 0s;
}

.current-menu-item span:before {
  width: 105%;
  transition: width 0.5s cubic-bezier(.22,.61,.36,1);
}

.current-menu-item span:after {
  background: transparent;
  width: 100%;
  transition: 0s;
}

.desktop-hide {
  display: none;
}

.landing-grid {
  position: relative;
  left: 10rem;
  right: 10rem;
  top: 16rem;
  bottom: 16rem;
}

.left-container {
  width: 50%;
  float: left;
}

.left-container,
.left-container .left-contents {
  position: relative;
  height: 100%;
}

.left-container .left-contents:before {
  display: block;
  content: "";
  width: 100%;
  padding-top: 136.8%;
}

.left-container .left-contents > .content {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.left-container .left-contents img {
  float: right;
  display: block;
  max-height: 100%;
}

.left-container .left-contents .content {
  left: auto;
}

.right-container {
  width: 50%;
  float: left;
}

.right-container,
.right-container .right-contents {
  position: relative;
  height: 100%;
}

.right-container .right-contents:before {
  display: block;
  content: "";
  width: 100%;
  padding-top: 136.8%;
}

.right-container .right-contents > .content {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
}

.right-container .right-contents img {
  float: left;
  display: block;
  max-height: 100%;
}

.right-container .right-contents .content {
  right: auto;
}

.carousel,
.carousel .carousel-inner,
.carousel .carousel-inner .item {
  height: 100%;
}

.landing-link {
  position: absolute;
  z-index: 1;
  left: 50%;
  bottom: 7rem;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
}

.landing-link span {
  position: relative;
  cursor: pointer;
  width: inherit;
  z-index: 2;
}

.landing-link span:after,
.landing-link span:before {
  content: "";
  position: absolute;
  width: 0;
  height: 4px;
  top: -10%;
}

.landing-link span:before {
  left: -2.5px;
}

.landing-link span:after {
  right: 2.5px;
  transition: width 0.8s cubic-bezier(.22,.61,.36,1);
}

.landing-link span.active:before,
.landing-link span:hover:before {
  width: 105%;
  transition: width 0.5s cubic-bezier(.22,.61,.36,1);
}

.landing-link span:hover:after span.active:after {
  background: transparent;
  width: 100%;
  transition: 0s;
}

.landing-link a {
  color: #eeb7b5;
  font-size: 2rem;
  text-align: center;
  font-family: FranklinBold;
  text-transform: uppercase;
  text-decoration: none;
  font-weight: 700;
  position: relative;
  z-index: 3;
}

.landing-link a:focus,
.landing-link a:hover {
  color: #eeb7b5;
}

p {
  font-size: 1.3rem;
  line-height: 1.62em;
  margin: 0 0 10px !important;
}

#mc_embed_signup div.mce_inline_error {
  background-color: #eeb7b5!important;
  color: #152d1b!important;
}

#mce-success-response {
  width: 30rem;
}

#mc_embed_signup_scroll h2 {
  margin-bottom: 5rem;
  letter-spacing: 1px;
  color: #152d1b;
  font-size: 20px;
  text-transform: uppercase;
  font-family: FranklinBold!important;
  text-align: center;
}

#mc_embed_signup_scroll .mc-field-group {
  text-align: center;
  color: #152d1b;
  font-size: 1.6rem;
  font-family: FranklinBold!important;
}

#mc_embed_signup_scroll input {
  background: #eeb7b5;
  border: 1px solid #152d1b;
  color: #152d1b;
  text-size: 1.6rem;
  text-transform: uppercase;
  border-radius: 0;
  text-align: center;
  height: 4.2rem;
  width: 35rem;
}

#mc_embed_signup_scroll .mc-field-group {
  margin-bottom: 10px;
}

#mc_embed_signup_scroll .button {
  color: #eeb7b5;
  width: 100%;
  background: #152d1b;
  line-height: 2rem;
  font-family: FranklinBold;
}

#mc_embed_signup_scroll .button:hover {
  color: #152d1b;
  background: #eeb7b5;
}

.img-contain {
  position: relative;
  max-width: 200px;
  max-height: 150px;
  margin: 25px auto;
}

::-webkit-input-placeholder {
  color: #152d1b;
  padding-left: 5px;
}

::-moz-placeholder {
  color: #152d1b;
}

:-ms-input-placeholder {
  color: #152d1b;
}

:-moz-placeholder {
  color: #152d1b;
}

.clearfix {
  clear: both;
}

#mc-embedded-subscribe {
  letter-spacing: 1px;
  color: #e0bb97;
  font-size: 15px;
  text-transform: uppercase;
  cursor: pointer;
  right: 50%;
  outline: none;
  position: relative;
  left: 50%;
  -webkit-transform: translateX(-50%);
  transform: translateX(-50%);
  margin-top: 20px;
  font-weight: 700;
}

#mc-embedded-subscribe:hover {
  background: #152d1b;
  border: 1px solid #152d1b;
  color: #eeb7b5;
  outline: none;
}

.num {
  text-align: center;
  margin-top: 3rem;
  margin-bottom: 1rem;
  font-size: 1rem;
  font-family: FranklinBold;
}

#myCarousel {
  margin: 0 auto;
  max-width: 50rem;
}

.h-margin-top {
  margin-top: 3rem;
}

.contact-details {
  margin-top: 2rem;
  text-align: center;
}

.contact-details p {
  font-weight: 700;
}

.contact-details span {
  position: relative;
  cursor: pointer;
  width: inherit;
}

.contact-details span:after,
.contact-details span:before {
  content: "";
  position: absolute;
  width: 0;
  height: 1px;
  top: 49%;
  margin-top: -.5px;
}

.contact-details span:before {
  left: -2.5px;
}

.contact-details span:after {
  right: 2.5px;
  transition: width 0.8s cubic-bezier(.22,.61,.36,1);
}

.contact-details span.active:before,
.contact-details span:hover:before {
  width: 100%;
  transition: width 0.5s cubic-bezier(.22,.61,.36,1);
}

.contact-details span:hover:after span.active:after {
  background: transparent;
  width: 100%;
  transition: 0s;
}

.contact-item {
  color: #eeb7b5;
  display: block;
}

.contact-item:focus,
.contact-item:hover {
  color: #eeb7b5;
}

.footer {
  width: 100%;
  margin-top: 2rem;
  position: absolute;
  bottom: 2rem;
  display: block;
}

.social {
  width: 10.2rem;
  margin: 0 auto;
}

.img-responsive {
  position: relative;
  width: 100%;
}

.fa-instagram,
.fa-twitter {
  font-size: 2.3rem!important;
  margin-right: 1.4rem;
  margin-top: 0.5rem;
}

.fa-facebook {
  font-size: 1.9rem!important;
  margin-right: 1.4rem;
  margin-top: 0.5rem;
}

.carousel-control .glyphicon-chevron-left,
.carousel-control .icon-previous {
  margin-left: -100px !important;
  color: pink!important;
  font-size: 20px!important;
  width: 20px!important;
  height: 20px!important;
}

.carousel-control .glyphicon-chevron-right,
.carousel-control .icon-next {
  margin-right: -100px !important;
  color: pink!important;
  font-size: 20px!important;
  width: 20px!important;
  height: 20px!important;
}

.max {
  max-width: 20rem;
}

.item {
  img {
    width: 100%;
    height: auto;
  }
}
@media only screen and (max-width:992px) {
  .carousel-control .glyphicon-chevron-left,
  .carousel-control .icon-previous {
    margin-left: -15px !important;
  }

  .carousel-control .glyphicon-chevron-right,
  .carousel-control .icon-next {
    margin-right: -15px !important;
  }

  .mobile-hide {
    display: none!important;
  }

  .desktop-hide {
    display: block;
  }

  .landing-grid {
    min-height: 1px;
    top: auto;
    left: auto;
    bottom: auto;
    right: auto;
    position: relative;
    -webkit-transform: translateX(0) translateY(0);
    transform: translateX(0) translateY(0);
  }

  .mobile-logo {
    position: relative;
    top: 3rem;
    left: 0;
    margin: 0 auto;
  }

  .right-container {
    width: 100%;
    padding-right: 20px;
    padding-left: 20px;
    padding-bottom: 20px;
  }

  .landing-link {
    position: absolute;
    z-index: 1;
  }

  .landing-link a {
    font-size: 5rem;
  }

  .landing-link span:after,
  .landing-link span:before {
    height: 10px;
    top: -106%;
  }

  header {
    height: 20rem;
  }

  #mc_embed_signup_scroll .button {
    width: 35rem;
  }

  .main {
    position: relative;
    left: auto;
    transform: none;
    &-landing {
      height: auto;
      width: auto;
    }
  }

  .nav {
    position: absolute;
    top: 3rem;
    left: 60%;
  }

  .nav ul li {
    display: block;
    text-align: right;
    width: 100%;
    font-size: 1.7rem;
    line-height: 4.2rem;
  }

  .nav ul li a {
    color: inherit;
    text-decoration: none;
  }

  .nav span:after,
  .nav span:before {
    height: 4px;
  }

  .nav .fullpage {
    margin-top: 2rem;
  }

  .nav .max {
    max-width: 100%;
    text-align: center;
  }

  .nav .margin-top {
    margin-top: 1rem;
  }

  .num {
    margin-bottom: 3rem;
  }

  .contact-details {
    font-size: 1.6rem;
    text-align: center;
    padding-bottom: 2rem;
  }

  .contact-details .contact-item {
    line-height: 4rem;
  }

  .footer {
    position: relative;
  }

  .margin-top {
    margin-top: 2rem;
  }

  .btn-subscribe {
    font-size: 1.2rem;
    max-width: 20rem;
  }
}
